@use "base/variables" as v;
@use "base/mixins" as m;
@use 'sass:color';

.video {
    position: relative;
    min-height: 50rem;
    max-height: 70rem;
    overflow: hidden;
    .overlay {
        position: absolute;
        background-color: rgba(color.adjust(v.$morado, $lightness: -20%), 0.9);
        inset: 0; 
        display: flex;
        align-items: center;
    };
    video {
        display: block;
        width: auto;
        @include m.desktop {
            width: 100%;
        }
    }
}

.contenido-video {
    z-index: 10;
    h2 {
        font-size: 7rem;
        color: v.$blanco;
        margin: 0;
    }

    p{
        background-color: v.$rosa;
        display: inline-block;
        padding: 1rem 2rem;
        font-size: 2.4rem;
        font-weight: 700;
        margin: 0;
        color: v.$blanco;
    }

    }